%space-between-content {
  display: flex;
  justify-content: space-between;
}

%column-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

%centered-content {
  display: flex;
  justify-content: center;
  align-items: center;
}

%space-around-content {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  justify-content: space-around;
}

%width-height {
  width: 100%;
  height: 100%;
}